.user {
    display: -ms-flexbox;
    display: flex;
}

.user__avatar {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    margin: 0 auto;
    margin-bottom: 10px;
    overflow: hidden;
}

.user__avatar img {
    display: block;
    width: 100%;
    height: 100%;
    -o-object-fit: cover;
       object-fit: cover;
}

.user__info {
    width: 15%;

    margin-right: 30px;

    text-align: center;
}

.user__repositories {
    position: relative;

    -ms-flex: 1 1 auto;

        flex: 1 1 auto;
}

.user__info a {
    display: block;
    padding: 5px 10px;
    border-radius: 4px;
    background-color: transparent;
    color: var(--color-black);
    text-decoration: none;
    -webkit-transition: .2s;
    -o-transition: .2s;
    transition: .2s;
}



