.header {
  padding: 0 30px;
  color: var(--color-white);
}

.header .content {
  padding: 50px 30px 30px;
  border-bottom-right-radius: 8px;
  border-bottom-left-radius: 8px;
  background-image: -webkit-gradient(linear, left bottom, left top, color-stop(60%, var(--primary-color)), to(var(--primary-color-d)));
  background-image: -webkit-linear-gradient(bottom, var(--primary-color) 60%, var(--primary-color-d) 100%);
  background-image: -o-linear-gradient(bottom, var(--primary-color) 60%, var(--primary-color-d) 100%);
  background-image: linear-gradient(to top, var(--primary-color) 60%, var(--primary-color-d) 100%);
  background-color: var(--primary-color);
}



