.users__count {
    margin-bottom: 30px;
}

.users__row {
    padding: 20px;
    margin-bottom: 30px;
    border: 1px solid lightgrey;
    border-radius: 5px;
    -webkit-transition: .2s;
    -o-transition: .2s;
    transition: .2s;
}

.users__row:hover {
    -webkit-box-shadow: 0 0 4px 0 lightgrey;
            box-shadow: 0 0 4px 0 lightgrey;
}


