.search {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  overflow: hidden;
  padding-bottom: 3px;
}

.search .input-text {
  -ms-flex-positive: 1;
      flex-grow: 1;
  display: block;
  padding: 10px;
  border: none;
  background-color: rgba(255, 255, 255, 0);
  -webkit-box-shadow: 0 1px 0 0 white;
          box-shadow: 0 1px 0 0 white;
  -webkit-transition: .3s ease-in-out;
  -o-transition: .3s ease-in-out;
  transition: .3s ease-in-out;
  outline: none;
  font-family: var(--main-font);
  font-size: 20px;
  color: white;
}

.search .input-text:focus {
  -webkit-box-shadow: 0 2px 0 0 white;
          box-shadow: 0 2px 0 0 white;
  background-color: rgba(255, 255, 255, .2);
}

.search .input-text:focus ~ .submit {
  opacity: 1;
  pointer-events: all;
}

.search .input-text:focus ~ .message {
  margin-left: -170px;
  opacity: 0;
}

.search .message {
  -ms-flex-order: -1;
      order: -1;
  width: 170px;
  opacity: 1;
  -webkit-transition: .3s ease-in-out;
  -o-transition: .3s ease-in-out;
  transition: .3s ease-in-out;
}

.search .submit {
  position: absolute;
  right: 0;
  top: 0;
  display: block;
  width: 40px;
  height: 100%;
  border: none;
  padding: 7px;
  background-color: transparent;
  color: white;
  cursor: pointer;
  outline: none;
  opacity: 0;
  pointer-events: none;
  -webkit-transition: .3s ease-in-out;
  -o-transition: .3s ease-in-out;
  transition: .3s ease-in-out;
}

.search .submit:focus,
.search .submit:hover {
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
}



